import React from "react";
import { Link } from "gatsby";

import FooterLogo from "../../assets/common/footer-logo.svg";

import ContentContainer from "../common/contentContainer";
import ContactLinks from "./contactLinks";
import { TLinksMarkup } from "../../types/commonMarkup.type";
import { ILang } from "../../types/common.type";

interface IFooterProps extends ILang {
  links: TLinksMarkup;
}

const Footer = ({ links, lang }: IFooterProps) => {
  const homePageLinkLabel =
    lang === "ru" ? "Домашняя страница КоудКэйв" : "CodeCave home page";

  return (
    <footer className="bg-main-100 border-t-4 border-secondary-100 border-solid p-4">
      <ContentContainer className="min-h-[8.5rem]">
        <nav className="w-full flex items-center flex-col md:flex-row">
          {/* Logo */}
          <Link
            to="/"
            aria-label={homePageLinkLabel}
            className="flex flex-1 items-center justify-start basis-1/4"
          >
            <FooterLogo />
          </Link>

          {/* Main links */}
          <ul className="flex justify-center flex-wrap shrink grow">
            {Object.entries(links).map(([link, title], i) => (
              <li key={`mainLinks-${i}`} className="p-2 w-max">
                <Link
                  to={`/${link.toLowerCase().split(" ").join("-")}`}
                  aria-label={`CodeCave ${title.toLowerCase()}`}
                  className="text-xl text-secondary-30 font-bold hover:underline"
                >
                  {title.toUpperCase()}
                </Link>
              </li>
            ))}
          </ul>

          {/* Contacts menu */}
          <div
            className="flex flex-wrap shrink basis-1/3 justify-center gap-2"
            itemScope
            itemType="https://schema.org/ContactPoint"
            itemProp="contactPoint"
          >
            <ContactLinks invert />
          </div>
        </nav>
      </ContentContainer>
    </footer>
  );
};

export default Footer;
